@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}table{border-collapse:collapse;border-spacing:0}



@font-face{font-family:'Nunito';src:url("https://fonts.googleapis.com/css2?family=Nunito&display=swap") format("truetype");font-weight:normal;font-style:normal}

*{font-family:"Nunito", sans-serif;font-size:16px;font-style:normal;font-weight:400;line-height:26px}body{background-color:#F8F8F8}

.container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;padding-bottom:100px}

.navbar{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;height:60px;background-color:white}@media (min-width: 360px){.navbar{padding:0 16px}}@media (min-width: 719px){.navbar{padding:0 32px}}@media (min-width: 919px){.navbar{padding:0 60px}}@media (min-width: 1183px){.navbar{padding:0 60px}}.nav{display:-webkit-flex;display:flex;grid-gap:10px;gap:10px}

.action_button{width:100px;padding:4px;background:rgba(244,224,65,0.87);border:none;border-radius:80px}.action_button:hover{background:#FFE302;cursor:pointer}.action_button:disabled{background:#B4B4B4;color:rgba(255,255,255,0.87)}

.header{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;box-sizing:border-box;max-width:1170px;width:100%;background-image:linear-gradient(0deg, rgba(0,0,0,0.5) 0%, rgba(0,0,0,0.5) 100%),url(../../static/media/pexels-alexandr-podvalny-1227513.be7665f8.jpeg);background-size:cover;background-position:center}@media (min-width: 360px){.header{height:500px}}@media (min-width: 719px){.header{height:500px}}@media (min-width: 919px){.header{height:650px}}@media (min-width: 1183px){.header{height:650px}}.header_title{font-family:"Nunito", sans-serif;font-size:40px;font-style:normal;font-weight:400;line-height:40px;text-align:center;padding-bottom:21px;color:#FFF}@media (min-width: 360px){.header_title{max-width:328px}}@media (min-width: 719px){.header_title{max-width:380px}}@media (min-width: 919px){.header_title{max-width:380px}}@media (min-width: 1183px){.header_title{max-width:380px}}.header_text{text-align:center;padding-bottom:32px;color:#FFF}@media (min-width: 360px){.header_text{max-width:328px}}@media (min-width: 719px){.header_text{max-width:380px}}@media (min-width: 919px){.header_text{max-width:380px}}@media (min-width: 1183px){.header_text{max-width:380px}}

.list{display:grid;grid-template-columns:repeat(2, 1fr);justify-items:center;width:100%;padding:140px 0}.list_item{display:-webkit-flex;display:flex;grid-column-start:1;grid-column-end:3}.list_item--button{-webkit-justify-content:center;justify-content:center}.list_item--title{font-family:"Nunito", sans-serif;font-size:40px;font-style:normal;font-weight:400;line-height:40px;text-align:center}.list_item--container{display:grid;grid-template-columns:repeat(1, 1fr);justify-items:center;grid-row-gap:20px;row-gap:20px;grid-column-gap:20px;-webkit-column-gap:20px;column-gap:20px;padding:50px 0}@media (min-width: 719px){.list_item--container{display:grid;grid-template-columns:repeat(2, 1fr);grid-row-gap:16px;row-gap:16px;grid-column-gap:16px;-webkit-column-gap:16px;column-gap:16px}}@media (min-width: 919px){.list_item--container{display:grid;grid-template-columns:repeat(3, 1fr);grid-row-gap:29px;row-gap:29px;grid-column-gap:29px;-webkit-column-gap:29px;column-gap:29px}}@media (min-width: 1183px){.list_item--container{display:grid;grid-template-columns:repeat(3, 1fr);grid-row-gap:29px;row-gap:29px;grid-column-gap:29px;-webkit-column-gap:29px;column-gap:29px}}

.card{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;box-sizing:border-box;width:100%;padding:20px;background:#FFF;border-radius:10px}@media (min-width: 360px){.card{max-width:328px}}@media (min-width: 719px){.card{max-width:344px}}@media (min-width: 919px){.card{max-width:282px}}@media (min-width: 1183px){.card{max-width:370px}}.card_info{width:100%;text-overflow:ellipsis;overflow:hidden;white-space:nowrap;text-align:center}.card_info--title{margin:20px 0}

.form{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;grid-gap:50px;gap:50px}@media (min-width: 360px){.form{width:328px}}@media (min-width: 719px){.form{width:380px}}@media (min-width: 919px){.form{width:380px}}@media (min-width: 1183px){.form{width:380px}}.form_title{font-family:"Nunito", sans-serif;font-size:40px;font-style:normal;font-weight:400;line-height:40px;text-align:center}.form_container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;grid-gap:50px;gap:50px}

.field{display:-webkit-flex;display:flex;position:relative;background:#F8F8F8;max-width:380px}.field_button{background-color:#F8F8F8;border-radius:4px 0 0 4px;border:1px solid rgba(0,0,0,0.87);padding:14px 15px}.field_button--error{border:2px solid #CB3D40}.field_button:hover{cursor:pointer;background:#D0CFCF}.field_input{width:100%;background-color:#F8F8F8;padding:14px 16px}.field_input--default{border-radius:4px;border:1px solid #D0CFCF}.field_input--default:focus{outline:none}.field_input--default--error{border:solid #CB3D40;border-width:2px 2px 2px 0}.field_input--upload{border-radius:0 4px 4px 0;border:solid #D0CFCF;border-width:1px 1px 1px 0}.field_input--upload:focus{outline:none}.field_input--upload--error{border:solid #CB3D40;border-width:2px 2px 2px 0}.field_label{display:block;visibility:hidden;background-color:#F8F8F8;padding:0 5px;position:absolute;top:-6px;left:16px;font-size:12px;line-height:14px;color:#7E7E7E}.field_label--visible{visibility:visible}.field_info{position:absolute;bottom:-18px;left:16px;font-size:12px;line-height:14px;color:#7E7E7E}.field_info--hidden{visibility:hidden}.field_info--error{color:#CB3D40}

.radio{display:-webkit-flex;display:flex;grid-gap:12px;gap:12px}.radio_button{display:none}.radio_label:before{content:url(../../static/media/radio.9793d870.svg);display:inline-block;position:relative;top:3px;margin:0 5px 0 0;width:20px;height:20px}.radio_button:checked+.radio_label:before{content:url(../../static/media/radio-active.7d33c13e.svg);display:inline-block;position:relative;top:3px;margin:0 5px 0 0;width:20px;height:20px}

